.messageInput{
    display: inline-block;
    box-sizing: border-box;
    box-shadow: none;
    border: 0;
    outline: 0;
    -webkit-appearance: none;
    appearance: none;
    text-overflow: ellipsis;
    line-height: 19px;
    font-size: 16px;
    color: var(--text_primary);
    width: calc(100% - 98px);
    position: relative;
    top: -7px;
    left: 4px;
    z-index: 2;
    background: 0 0;
}